@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300;400;600&display=swap');

$fontStyle: 'Josefin Sans', sans-serif;

#content {
    max-width: 100%;
    height: auto;
    padding: 5vmin 0 10vmin 10vmin;
    background-image: url("../bg-pattern-desktop.svg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: left;
}

#logo {
    width: 10vmin;
}

#title {
    font-size: 7vmin;
    font-weight: 600;
    letter-spacing: 1.8vmin;

    #pinkTitle {
        font-weight: 300;
        color: hsl(0, 36%, 70%);
    }
    #grayTitle {
        color: hsl(0, 6%, 24%);
    }
}


#text {
    font-weight: 400;
    color: hsl(0, 36%, 70%);
    letter-spacing: 0.1vmin;
}